export default function dnevnikReducer(state = initialState, action) {
    switch(action.type) {
        case CHANGE_DNEVNIK:
            return {
                    ...state,
                    [action.payload.stateId]: action.payload.cState
                };
        case CHANGE_DNEVNIK_DAY_UP:
            return {
                    ...state,
                    days: {
                        [action.payload.stateId]: action.payload.cState,
                        ...state.days
                    }
                };
        case CHANGE_DNEVNIK_DAY_DOWN:
            return {
                ...state,
                days: {
                    ...state.days,
                    [action.payload.stateId]: action.payload.cState
                }
            };
        default:
            return state;
    }
}